import { Card, Image } from "antd";
const { Meta } = Card;

function PlayerCard({ name, image }) {
  return (
    <div className="Player cardButton">
      <Card
        style={{
          width: 340,
          height: 260,
        }}
        cover={
          image === "" ? (
            <Image
              preview={false}
              src="error"
              fallback="data:image/png;base64,"
            />
          ) : (
            <Image src={image} />
          )
        }
      >
        <Meta title={name} />
      </Card>
    </div>
  );
}

export default PlayerCard;
